@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
.navbar-header{display:flex;align-items:center;height:10vh;width:100%;color:#f9f9f9}.navbar-logo{width:10%}.navbar-content{width:90%;display:flex;justify-content:space-between;align-items:center}.navbar-item,.navbar-logo,.navbar-account{padding:0.5rem;margin:0 0.5rem}.navbar-item{text-decoration:none;color:#f9f9f9;position:relative}.navbar-item i{margin-right:0.5rem}.navbar-item .underline::after{content:'';border-bottom:solid 1.5px #f9f9f9;position:absolute;bottom:0;right:0;width:65%;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:right;transform-origin:right;-webkit-transition:-webkit-transform 0.25s;transition:-webkit-transform 0.25s;transition:transform 0.25s;transition:transform 0.25s, -webkit-transform 0.25s}.navbar-item .underline:hover::after{-webkit-transform-origin:left;transform-origin:left;-webkit-transform:scaleX(1);transform:scaleX(1)}.navbar-pages{display:flex}

.video-card{position:relative;margin:0 auto;width:97%;height:185px;border:none;border-radius:6px;overflow:hidden;-webkit-transition:all 300ms ease-in;transition:all 300ms ease-in}.video-card.video-header{height:440px}.video-card .video-grid-title,.video-card .video-header-title{position:absolute;padding:1rem;cursor:pointer;color:#fff;z-index:5;text-shadow:white 0px 0px 10px}.video-card .video-grid-title{top:5px;right:0;font-size:1.1rem}.video-card .video-header-title{left:0;bottom:30px;margin-left:1rem;font-size:3rem}.video-card:hover{border:3px solid #ecf0f1;-webkit-transform:scale(1.05);transform:scale(1.05)}.video-card-image{width:100%;height:100%;object-fit:cover;-webkit-filter:brightness(85%);filter:brightness(85%)}.video-card-image:hover{-webkit-filter:brightness(100%);filter:brightness(100%)}@media screen and (min-width: 800px){.video-card{height:225px}}@media screen and (min-width: 1000px){.video-card{height:275px}}@media screen and (min-width: 1250px){.video-card{height:300px}}@media screen and (min-width: 1500px){.video-card{height:325px}.video-card .video-header-title{font-size:4rem}.video-card .video-grid-title{left:0;top:30px;font-size:2rem}}

.video-header-slide-show{margin-bottom:2rem}.video-header-slide-show .slick-dots{width:30%;height:40px;left:62%;top:420px}.video-header-slide-show .slick-slide{padding:25px}.video-header-slide-show .slick-list{margin:0 0.5rem}.video-header-slide-show .slick-dots li button::before{font-size:10px;color:#161722;opacity:0.8}.video-header-slide-show .slick-dots li.slick-active button::before{color:#fff;opacity:1}@media screen and (max-width: 1250px){.video-header-slide-show .slick-dots{width:35%;left:55%}}@media screen and (max-width: 1035px){.video-header-slide-show .slick-dots{width:40%;left:50%}}@media screen and (max-width: 915px){.video-header-slide-show .slick-dots{height:35px;width:50%;left:40%}}@media screen and (max-width: 727px){.video-header-slide-show .slick-dots{width:100%;left:0}}


.video-grid-title{padding:1rem;margin-top:2rem}

.homepage-container{max-width:2000px;margin:0 auto}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 0.5em;
  height: 0.5em;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 3px;
}

body {
  background-color: #161722;
  color: #fff;
  line-height: 1.5;
  height: 100vh;
  font-family: Nunito, sans-serif;
}

/* React slick  */
.dotsClass {
  color: #fff;
}

.slick-dots li button::before {
  font-size: 8px;
  color: #eee;
  opacity: 0.7;
}

.slick-dots li.slick-active button::before {
  color: black;
  opacity: 1;
}

.slick-slide {
  padding: 1rem 8px;
}

.slides .slick-prev,
.slides .slick-next {
  position: absolute;
  top: 50%;
  z-index: 10;
}

.slick-slider.slick-initialized {
  position: relative;
}

.slick-arrow.slick-prev,
.slick-arrow.slick-next {
  position: absolute;
  height: 90%;
  z-index: 100;
  padding: 0 2rem;
  margin: 0 auto;
  opacity: 0;
  -webkit-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
  background-color: transparent;
}

.slick-arrow.slick-prev {
  left: 5px;
}

.slick-arrow.slick-next {
  right: 5px;
}

.slick-arrow.slick-prev:hover,
.slick-arrow.slick-next:hover {
  opacity: 0.75;
}

@media screen and (min-width: 501px) {
  .slick-arrow.slick-prev {
    padding-right: 3rem;
    padding-left: 1rem;
  }
  .slick-arrow.slick-next {
    padding-right: 2.3rem;
    padding-left: 1.7rem;
  }
}

@media screen and (max-width: 500px) {
  .slick-arrow.slick-prev,
  .slick-arrow.slick-next {
    opacity: 1;
  }

  .slick-arrow.slick-next {
    padding-right: 2.5rem;
    padding-left: 1.5rem;
  }

  .slick-arrow.slick-prev {
    padding-right: 3rem;
    padding-left: 1rem;
  }

  .slick-arrow.slick-prev:hover,
  .slick-arrow.slick-next:hover {
    opacity: 0.99;
  }
}

